#App {
    position: relative;
    height: 100%;
    background: #000;
    color: #fff;
    font-size: 50px;
    line-height: 1.2;
    .center {
        position: absolute;
        width: 100%;
        text-align: center;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
    .code-box-shape {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);
        font-size: 45px;
    }
}